.section_comand_lis{
  margin: 0 auto;
  padding: 0;
  width: 960px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-direction: row;
}

.comand_item{
  position: relative;
  margin: 0;
  padding: 0;
  width: 237px;
height: auto;
margin-bottom: 4px;

  overflow: hidden;

}

.transition_slider{
  opacity: 0;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
 
    background: rgba(34,34,34,.8);
    background-color: rgba(0,0,0,.3);
}
.transition_slider:hover{
  opacity: 1;
  transition: 0.8s;
}

.block_button{
  position: absolute;
height: 120px;

  bottom: 0;
  left: 0;
  right: 0;
  max-width: 100%;
}


.item_name{
  padding-left: 6px;
  display: block;
  position: relative;
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 10px;
    color: #fff;
   
}

.comand_item_position{
  padding-left: 6px;
  display: block;
  position: relative;
  font-size: 14px;
  margin-bottom: 20px;
  color: #fff;
}

.comand_item_more{
  display: block;
  border: 1px solid rgb(96, 102, 96);
  background-color: rgb(174, 174, 177);
margin: 0;
padding: 0;
height: 25px;
align-items: center;
text-align: center;
width: 150px;
    position: relative;
    color: #fff;
    color: #444;
    font-size: 14px;
    font-weight: 400;
    text-transform: none;
    cursor: pointer;
    margin-left: 6px;
}


.comand_item_more:hover{
  color: white;
  border: 1px solid rgb(64, 170, 64);
  background-color: rgb(163, 202, 168);
}

.section_back_color{
  background-color: #f7f6f4;
}